.App{
  min-height: 100vh;
  background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%); 
}


.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}


.search-bar{
  width: 20%;
  border: none;
  padding: 10px;
}


.search-button{
  background: navy;
  border: none;
  padding: 10px 20px;
  color: white; 
}


.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}